/* src/App.css */
body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #f4f7f6;
  color: #333;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
}

.app-container {
  background-color: #ffffff;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  padding: 30px;
  width: 100%;
  max-width: 750px;
  box-sizing: border-box;
  text-align: center;
}

.app-title {
  color: #2c3e50;
  font-size: 2.2em;
  margin-bottom: 25px;
  font-weight: 700;
}

.main-panel {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 30px;
}

.panel-title {
  color: #34495e;
  font-size: 1.4em;
  margin-bottom: 15px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
}

.convert-button {
  background-color: #4682b4; /* Steel Blue */
  color: white;
  padding: 14px 25px;
  font-size: 1.1em;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.convert-button:hover {
  background-color: #35709c;
  transform: translateY(-2px);
}

.convert-button:active {
  transform: translateY(0);
}

.contact-info {
  margin-top: 40px;
  padding-top: 20px;
  border-top: 1px solid #e0e0e0;
  font-size: 0.9em;
  color: #555;
}

.contact-info h3 {
  margin: 0 0 8px 0;
  color: #2c3e50;
}

.contact-info p {
  margin: 4px 0;
}
